import { DeepPartial } from '../core'
import { User } from './user.model'

/**
 * User Mock
 */
export function UserMock(options?: DeepPartial<User>): User {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'USER_ID',

    name: options?.name || 'USER_NAME',
    email: options?.email,

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
